import React, { Component } from 'react';
import { NavLink } from 'react-router-dom';

import FixedFormatPrice from '../fixedFormatPrice';

class ShowCartProducts extends Component {
    constructor(props) {
        super(props)

        this.state = {
            price: 0,
            notificationsAddToCart: 'hide',
            test: 1,
            checkAdd: false
        }
    }

    componentWillUpdate(nextState) {
       // console.log(this.props.notificationColorHeaderCart)

        if (this.props.notificationColorHeaderCart === null) {
            console.log('here')
            if (nextState.count === this.state.test) {
                this.setState({
                    notificationsAddToCart: 'hide',
                    test: this.state.test += 1,
                    checkAdd: true
                })
            } else if (this.state.checkAdd) {
                this.setState({
                    notificationsAddToCart: 'show',
                    checkAdd: false
                })
            }
        } else {
            if (this.state.test > 1) {
                this.setState({
                    test: 1
                })
            }
        }
    }

    render() {
        let orderTotal = 0;
        this.props.cart.map((product) => {
            let a = product.price;
            let b = product.countOrder;
            let result = a * b;
            orderTotal += result;
        })
        return (
            <li className={this.state.notificationsAddToCart}><NavLink to="/cart"
            ><div className="count_product">{this.props.count}</div><i className="fas fa-shopping-cart"></i>
                <div className="productNumsHeader">
                    <span>${FixedFormatPrice.formatCurrency(orderTotal)}</span>
                </div>
            </NavLink>
            </li>
        )
    }

}
export default ShowCartProducts;
