import React, { Component } from 'react';
import { Link } from 'react-router-dom';
import styled from 'styled-components';

const ContainerInfoClickedProductCart = styled.div`
    left: ${props => props.infoClikedProductCart}%;
`;

class ClickedInfo extends Component {
    render() {
        let { product } = this.props;
        return (
            
            <ContainerInfoClickedProductCart className="infoClikedProductCart" infoClikedProductCart={this.props.infoClikedProductCart}>
                <div className="image_box">
                    <img src={product.picture} alt="" />
                    <h3>{product.itemOfModel_name}</h3>
                </div>
               
                <div className="info_box">
                    <h4>Specifications</h4>
                    
                    <div className="info_box_text">
                        <h5>In progress...</h5>
                    </div>
                </div>
                <Link className="btnSlideHide" onClick={(e) => this.props.hideInfoProduct('-200')}><i class="fas fa-angle-double-left"></i></Link>
            </ContainerInfoClickedProductCart>
        )
    }
}
export default ClickedInfo;
